<script>
import qs from 'qs'
import boyTeacher from '@/assets/img/boy_teacher.jpg'
import { mapState } from 'vuex'
export default {
  name: 'Chatroom',
  // props: {
  //   readonly: {
  //     type: Boolean,
  //     default: false
  //   },
  //   selectTimeDefault: {
  //     type: String,
  //     default: null
  //   },
  //   selectDateDefault: {
  //     type: Array,
  //     default: null
  //   }
  // },
  data: function () {
    return {
      boyTeacher,
      websock: null,
      messageText: ''
    }
  },
  // created () {
  //   this.initWebSocket()
  // },
  // destroyed () {
  //   this.websock.close()
  // },
  computed: {
    ...mapState(['loginStatus', 'doChatView', 'doChatAction', 'doChatroomID']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  watch: {
    loginStatus (val) {
      // if (val) {
      //   this.getHistoricalMessage()
      //   this.initWebSocket()
      // } else {
      //   alert('')
      //   this.websock.close()
      //   this.$router.go(0)
      // }
    },
    doChatView (val) {
      if (val) {
        this.doOpenChatWindows()
      }
    }
  },
  mounted () {
    if (localStorage.getItem('token')) {
      this.getHistoricalMessage()
      this.initWebSocket()
    } else {
      alert('')
      this.websock.close()
      this.$router.go(0)
    }
  },
  methods: {
    getHistoricalMessage () {
      alert('')
      if (localStorage.getItem('token')) {
        alert('')
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          user_type: this.userInfo.type
        }
        this.$http.post('/api/chat/contentAndHistoryOfChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = ''
              alert('')
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    initWebSocket () {
      const wsuri = 'ws://127.0.0.1:8000/ws/chat/1_chatroom_1/'
      this.websock = new WebSocket(wsuri)
      this.websock.onmessage = this.websocketonmessage
      this.websock.onopen = this.websocketonopen
      this.websock.onerror = this.websocketonerror
      this.websock.onclose = this.websocketclose
      console.log(this.websock)
    },
    websocketonerror () { // 
      // this.initWebSocket()
    },
    websocketonopen () { // send
      alert('')
    },
    websocketonmessage (e) { // 
      const redata = JSON.parse(e.data)
      console.log(e.data)
      console.log(redata)
    },
    websocketsend () { // 
      let msg = {
        userID: 1,
        chatID: 1,
        messageType: 0,
        messageText: this.messageText,
        message: '-' + this.messageText
      }
      this.websock.send(JSON.stringify(msg))
      alert('')
    },
    websocketclose () {
      alert('')
    },
    doOpenChatWindows () {
      if (this.$store.state.doChatAction === 'message') {
        alert('' + this.$store.state.doChatroomID + '')
      }
    },
    doCloseChatWindows () {
      this.$store.state.doChatView = false
    }
  }
}
</script>
